@use 'base/variables' as v;
@use 'base/mixins' as m;

.destinos{

    &__grid{
        @include m.grid(1, 2rem);
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop {
            @include m.grid(4, 2rem);
        }
    }
}

.destino {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 4rem;
    &__imagen{
        width: 14rem;
        height: 14rem;
    }

    &__nombre,
    &__distancia{
        margin: 0;
    }

    &__nombre{
        font-size: 2rem;
    }

    &__distancia{
        font-size: 1.6rem;
    }
}